.top-company__item {
    margin-top: 8px;
    font-size: 20px;
}
.top-company {
    transition: box-shadow 0.3s;
    height: 100%;
    padding-top: 5;
    background-color: "#fff";
}

.top-company__item-img {
    width: 100%;
    height: 180px;
    object-fit: contain;
}

.top-company:hover {
    box-shadow: 0 0 11px rgba(33, 33, 33, 0.2);
}

.top-company__name {
    color: rgba(0, 0, 0, 0.87);
}
